.robo{
	background-color: rgba(179,185,193,255);
	padding: 73px 0 91px;
	box-sizing: border-box;
	position: relative;
	overflow: hidden;
	z-index: 0;
}

.robo__description{
	margin: 0 53px 0 auto;
}

.robo__table{
	margin: 69px auto -930px;
	max-width: 1297px;
	padding-left: 0;
	list-style: none;
	letter-spacing: 12px;
	text-align: center;
}

.robo__item{
	display: inline-block;
	position: relative;
	z-index: 10;
}

.robo__item-info{
	display: none;
}

.robo__img:hover ~ .robo__item-info{
	display: block;
}

.robo__item:hover{
z-index: 80;
}


.robo__decor-triangle{
	position: absolute;
	left: 0;
	top: 0;
	z-index: -1;
}

.robo__decor-right{
	position: absolute;
	right: 0;
	top: 233px;
	z-index: -1;
}

.robo__decor-bottom{
	position: absolute;
	right: 0;
	bottom: 6px;
	z-index: -1;
}

.robo__decor-lines{
	position: absolute;
	z-index: -1;
	left: 63px;
	bottom: 150px;

}


/*------------смещения-----------*/
.robo__item:nth-child(7n-6){
	margin-left: 12%;
}

.robo__item:nth-child(7n-4){
	margin-right: 12%;
}

.robo__item:nth-child(n+4):nth-child(-n+7){
	transform: translateY(-156px);
}

.robo__item:nth-child(n+8):nth-child(-n+10){
	transform:translateY(-308px);
}

.robo__item:nth-child(n+11):nth-child(-n+14){
	transform:translateY(-463px);
}

.robo__item:nth-child(n+15):nth-child(-n+17){
	transform:translateY(-618px);
}

.robo__item:nth-child(18){
	transform:translateY(-773px);
	margin-left: 15%;

}
.robo__item:nth-child(19){
	transform:translateY(-773px);
	margin-right: 15%;
}
.robo__item:last-child{
	transform:translateY(-928px);
}
/*-----смещения конец--------*/

